import {
  Button,
  Input,
} from "@ya.praktikum/react-developer-burger-ui-components";
import React, { ChangeEvent, useEffect, useState } from "react";
import { useFetchUserQuery, useUpdateUserMutation } from "../../utils/api";
import { useFormAndValidation } from "../../hooks/use-form-and-validation";
import profileForm from "./profile-form.module.css";

interface IFormValues {
  name: string;
  email: string;
  password: string;
}

const initialFormValues: IFormValues = {
  name: "",
  email: "",
  password: "",
};

const ProfileForm: React.FC = () => {
  const { data: userData, error, isLoading } = useFetchUserQuery(undefined);
  const [updateUser] = useUpdateUserMutation();
  const [isEditing, setIsEditing] = useState(false);
  const { values, handleChange, resetForm, setIsValid } =
    useFormAndValidation(initialFormValues);

  useEffect(() => {
    if (userData && userData.success) {
      const { user } = userData;
      const initialValues = {
        name: user.name || "",
        email: user.email || "",
        password: "",
      };
      resetForm(initialValues);
      setIsValid(true);
    }
  }, [userData, resetForm, setIsValid]);

  const handleInputChange = (event: ChangeEvent<HTMLInputElement>) => {
    handleChange(event);
    if (!isEditing) {
      setIsEditing(true);
    }
  };
  const handleSave = async () => {
    try {
      await updateUser(values).unwrap();
      setIsEditing(false);
    } catch (err) {
      console.error("   :", err);
      setIsValid(false);
    }
  };

  const handleCancel = () => {
    if (userData && userData.success) {
      const { user } = userData;
      resetForm({
        name: user.name || "",
        email: user.email || "",
        password: "",
      });
    }
    setIsEditing(false);
  };

  if (isLoading)
    return (
      <div className={profileForm.content}>
        <p className="text text_type_main-default">...</p>
      </div>
    );
  if (error)
    return (
      <div className={profileForm.content}>
        <p className="text text_type_main-default">
            .  .
        </p>
      </div>
    );

  return (
    <div className="mt-20 mb-6">
      <form>
        <Input
          type={"text"}
          placeholder={""}
          name={"name"}
          value={values.name || ""}
          onChange={handleInputChange}
          error={false}
          errorText={""}
          size={"default"}
          icon={"EditIcon"}
          extraClass="mb-6"
          onPointerEnterCapture={undefined}
          onPointerLeaveCapture={undefined}
        />
        <Input
          type={"email"}
          placeholder={""}
          name={"email"}
          value={values.email || ""}
          onChange={handleInputChange}
          error={false}
          errorText={""}
          size={"default"}
          icon={"EditIcon"}
          extraClass="mb-6"
          onPointerEnterCapture={undefined}
          onPointerLeaveCapture={undefined}
        />
        <Input
          type={"password"}
          placeholder={""}
          name={"password"}
          value={values.password || ""}
          error={false}
          errorText={""}
          size={"default"}
          icon={"EditIcon"}
          extraClass="mb-6"
          onChange={handleInputChange}
          onPointerEnterCapture={undefined}
          onPointerLeaveCapture={undefined}
        />
        {isEditing && (
          <div>
            <Button htmlType="button" onClick={handleSave}>
              
            </Button>
            <Button htmlType="button" onClick={handleCancel}>
              
            </Button>
          </div>
        )}
      </form>
    </div>
  );
};

export default ProfileForm;
