import {
  Button,
  Input,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { Link, useLocation, useNavigate } from "react-router-dom";
import loginStyle from "./login.module.css";
import { FormEvent, useEffect } from "react";
import { useAuth } from "../../components/auth-provider/auth-provider";
import { useFormAndValidation } from "../../hooks/use-form-and-validation";

const initialState = {
  email: "",
  password: "",
};
function LoginPage() {
  const { values, handleChange, errors, isValid, setValues, resetForm } =
    useFormAndValidation(initialState);
  const navigate = useNavigate();
  const location = useLocation();
  const { login, isAuthenticated, setVisitedForgotPassword } = useAuth();

  const handleLogin = async (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const { email, password } = values;

    if (!email || !password) {
      alert(",   ");
      setValues((prevValues) => ({
        ...prevValues,
        email: !email ? "" : prevValues.email,
        password: !password ? "" : prevValues.password,
      }));
    } else {
      try {
        await login(email, password);
        const from = location.state?.from?.pathname || "/";
        navigate(from, { replace: true });
        resetForm();
      } catch (error) {
        console.error(" :", error);
      }
    }
  };

  useEffect(() => {
    if (isAuthenticated) {
      navigate("/");
    }
  }, [isAuthenticated, navigate]);

  useEffect(() => {
    setVisitedForgotPassword(false);
  }, [setVisitedForgotPassword]);

  return (
    <div className={loginStyle.content}>
      <div className={loginStyle.box}>
        <h2 className={loginStyle.h2}></h2>
        <form className={`${loginStyle.form} mt-6 mb-6`} onSubmit={handleLogin}>
          <Input
            type={"email"}
            placeholder={"E-mail"}
            value={values.email || ""}
            name={"email"}
            error={Boolean(errors.email)}
            errorText={"  E-mail"}
            size={"default"}
            extraClass="mb-6"
            onChange={handleChange}
            onPointerEnterCapture={undefined}
            onPointerLeaveCapture={undefined}
            data-testid="email_input"
          />
          <Input
            type={"password"}
            placeholder={""}
            value={values.password || ""}
            name={"password"}
            error={Boolean(errors.password)}
            errorText={"  "}
            size={"default"}
            icon={"ShowIcon"}
            extraClass="mb-6"
            onChange={handleChange}
            onPointerEnterCapture={undefined}
            onPointerLeaveCapture={undefined}
            data-testid="password_input"
          />
          <Button
            htmlType="submit"
            disabled={!isValid}
            data-testid="login_button"
          >
            
          </Button>
        </form>
        <div className={`${loginStyle.questions} mt-20`}>
          <p className="text text_type_main-default text_color_inactive mb-4">
              ?{" "}
            <Link to="/register" className={loginStyle.link}>
              
            </Link>
          </p>
          <p className="text text_type_main-default text_color_inactive">
             ?{" "}
            <Link to="/reset-password" className={loginStyle.link}>
               
            </Link>
          </p>
        </div>
      </div>
    </div>
  );
}

export default LoginPage;
